@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Ubuntu", sans-serif;
}

h1 {
  text-align: center;
  margin-top: 4rem;
}
.container {
  width: 500px;
  margin: 2rem auto;
  text-align: center;
  padding: 3rem;
  border-radius: 5px;
  background-color: rgb(231, 254, 255);
}
.btn {
  color: black;
  background-color: rgb(33, 52, 94);
  padding: 0.7rem 1rem;
  margin: 1rem;
  color: white;
  font-weight: 600;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}

.button-box {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.button-box button {
  padding: 1rem 2rem;
  background-color: rgb(48, 186, 103);
  color: white;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  font-family: "Ubuntu", sans-serif;
}

.active {
  background: rgb(82, 113, 255);
  color: white;
}
